import { CodeIcon } from "@heroicons/react/solid";
import React from "react";
import { projects } from "../data";

export default function Projects() {
  return (
    <section id="projects" className="text-gray-40 body-font">
      <div className="container px-5 py-10 mx-auto text-center lg:px-40">
        <div className="flex flex-col w-full mb-20">
          <CodeIcon className="mx-auto inline-block w-10 mb-4" />
          <h1 className="sm:text-4xl text-3xl font-medium title-font mb-4 text-white">
            Mis Apps
          </h1>
          <p className="lg:w-2/3 mx-auto leading-relaxed text-base">
            Estos son mis ultimos bebes. 
            Un vistazo y un link ;)
          </p>
        </div>
        <div className="flex flex-wrap -m-4">
          {projects.map((project) => (
            <a
              href={project.link}
              key={project.image}
              className="sm:w-1/2 md:w-1/2 p-4">
              <div className="flex relative">
                <img
                  alt="gallery"
                  className="absolute inset-0 w-full h-full object-cover object-center"
                  src={project.image}
                />
                <div className="px-8 py-10 relative z-10 w-full border-4 bg-white opacity-0 transition duration-500 ease-in-out hover:-translate-y-1 hover:scale-110 hover:opacity-100 ">
                  <h2 className="tracking-widest text-md title-font font-medium text-indigo-400 mb-1">
                    {project.subtitle}
                  </h2>
                  <h1 className="title-font text-lg font-medium text-gray-800 mb-3">
                    {project.title}
                  </h1>
                  <p className="leading-relaxed text-gray-700">{project.description}</p>
                </div>
              </div>
            </a>
          ))}
        </div>
        <div className="flex justify-center">
          <a href="https://github.com/ya1k96?tab=repositories" className="bg-gray-500 flex flex-wrap justify-center items-center py-6 rounded shadow-3xl mt-3 w-1/3  transition duration-500 ease-in-out text-white transform hover:-translate-y-2 hover:scale-110 cursor-pointer">
              <h2 className="title-font text-center font-semibold text-gray-200  text-xs">
                My repo 
              </h2>
              <i className="ml-4 fab fa-2x fa-github"></i>                          
          </a>
        </div>
        </div>      
    </section>
  );
}